<template>
<div class="machine-content">
	<machine-item @machineClick="machineClick"  v-for="(item,index) in machinesArrs" :key="index" :index="index" :machine="item"/>
<van-overlay :show="show" @click="show = false" >
  <div class="wrapper" @click.stop>
    <div class="block" >
		<ul>
			<li>机器编号:<span>{{oneMachineInfo.machinenum}}</span></li>
			<li>机器名称:<span>{{oneMachineInfo.machinename}}</span></li>
			<li>开始时间:<span>{{oneMachineInfo.begintime}}</span></li>
			<li>预计时间:<span>{{oneMachineInfo.predictTime}}</span></li>
			<li>所需时间:<span>{{oneMachineInfo.time}}</span></li>
			<li>机器工作状态:<span>{{statusText[oneMachineInfo.status]}}</span></li>
			<li>机器是否启用：<span><van-switch v-model="checked" size="12px" @click="switchClick"/></span></li>
			<li>描述：<p>{{oneMachineInfo.remarks}}</p></li>
		</ul>
	<div class="button-box">
		<van-button type="primary" plain  :style="{marginRight:'20px'}" color="#516df5" @click="quxiao" class="close">关闭</van-button>
		<van-button type="primary" color="#516df5" @click="appointmentClick" class="close">预约</van-button>
	</div>
	</div>
  </div>
</van-overlay>

<van-popup
  v-model="showp"
  closeable
  position="bottom"
  :style="{ height: '20%' }"
>
<div class="propup-box">
<div class="btn-box"><van-button type="primary" @click="showClick" class="btn">查看详情</van-button></div>
<div  class="btn-box" ><van-button type="primary" @click="bookingClick" class="btn">预约</van-button></div>

</div>
</van-popup>
</div>
	
</template>

<script>
import MachineItem from './MachineItem'
import { Overlay,Button,DatetimePicker,Switch,Popup,} from 'vant';
import {updateStatus} from 'network/machine'
export default {
  name: 'MachineContent',
  components:{
	MachineItem,
	[Overlay.name]:Overlay,
	[Button.name]:Button,
	[DatetimePicker.name]:DatetimePicker,
	[Switch .name]:Switch ,
	[Popup .name]:Popup ,
  },
  data(){
	return {
show:false,
showp:false,
//typenma(机器名字),predictTime(预计时间),beginTime(开始时间)isFree(是否空闲)
oneMachineInfo:{},
minDate: new Date(2020, 0, 1),
maxDate: new Date(2025, 10, 1),
currentDate: new Date(),
timeShow:false,
statusText:['空闲','忙碌','准备完成'],
checked:true,
switchArr:['停用','启用'],
index:0,
	}
  },
  props:{
	machinesArr:{
		type:Array,
		default(){
			return []
		}
	}
  },
  methods:{
	quxiao(){
		this.show=false
	},
	machineClick(machine){
		if(machine.isuse==0){
			this.$Toast('抱歉，该机器维护中')
			//return
		}
		this.showp=true
		this.checked=machine.isuse==0?false:true
		this.oneMachineInfo=machine
		localStorage.setItem('machine',JSON.stringify(machine));
		//这里保存一下点击的下标
		this.mindex=machine.index
	},
	changes(value){
		console.log(value)
	},
	switchClick(){
		//这里使用ajax 操作数据库的开关
		let t=this.checked?0:1
		//这里查找该数组的id的下标
		
		this.machinesArr[this.mindex].isuse=t
		updateStatus(t,this.oneMachineInfo.id).then(res=>{
			console.log(res)
		})
		this.$Toast(this.switchArr[t])
	},
	appointmentClick(){
		//跳预约界面
		console.log('预约')
		this.$router.push({name:'subscribe',params:{machine:this.oneMachineInfo}}).catch(res=>{res})
	},
    showPopup() {
      this.showp = true;
    },
	//显示点击
	showClick(){
	this.show=true
      this.showp = false;
	},
	//修改点击
	bookingClick(){
		//这里可以使用登录用户 保存是谁预约的
		this.$router.push({name:'subscribe',params:{machine:this.oneMachineInfo}}).catch(res=>{res})
	}
	//状态
  },
  computed:{
	machinesArrs(){
		return this.machinesArr
	}
  },
  watch:{
	checked(){
		//console.log('发生了改变')
	}
  }
}
</script>

