  }
  .btn-box .btn{
	background:#f7f7f7;
	border:none;
	color:black;
	width:100%;
  }
  .propup-box{
	position:relative;
	top:38px;
  }
  .button-box{
	position:absolute;
	padding:15px 0;
	text-align:center;
	bottom:0;
	width:100%;
	left:0;
  }

