<template>
<div id="cart">
<van-nav-bar
  title="机器列表"
  right-text="新增机器"
  @click-right="onClickRight"
  class="nav"
/>
<div class="cart-vant-box">
<van-list
  :finished="finished"
  finished-text="没有更多了"
  @load="onLoad"
>
<machine-content :machines-arr="machines"/>
</van-list>
</div>
</div>
	
</template>

<script>
import { NavBar,List} from 'vant';		//这个可以封装
import MachineContent from './childComps/MachineContent'
import {getMachine} from 'network/machine'
export default {
  name: 'Cart',
  components:{
	[NavBar.name]:NavBar,
	[List .name]:List, 
	MachineContent,
  },
  data(){
	return{
		list:[],
		loading:false,
		finished:false,
		machines:[],
	}
  },
	methods:{
		onClickRight(){
			//this.$Toast('新增机器，跳转到添加机器页面')
			this.$router.push('machine').catch(res=>{res})
		},
onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },		
	//获取数据
	getMachine(){
		getMachine().then(res=>{
			//获取数据
			this.machines.push(...res)
		})
	}
	},
	mounted(){
		this.getMachine()
	}
}
</script>

<style scoped>
#cart{
	height:100vh;
}
.nav{
	background:#516df5;
}
/deep/ .van-ellipsis {
	color: white;
}
