<template>
<div class="machine-item" :class="mStaus.className" @click="machineClick">
	<img src="~assets/images/common/jiqi.svg" class="uimg">
	<div class="info-box-x">
		<div>机器编号：<span class="num">{{machine.machinenum}}</span></div>
		<div>机器名称：<span>{{machine.machinename}}</span></div>
		<div>工作状态：<span><font :color="mStaus.color">{{mStaus.statusType}}</font></span></div>
		<div>是否启用：<span>{{useText}}</span></div>
		<div>所需时间：<span>{{machine.time}}</span></div>
		<div>出品质量：<span><van-rate v-model="machine.stars" readonly /></span></div>
	</div>
</div>
	
</template>

<script>
import {Rate} from 'vant'

export default {
  name: 'MachineItem',
  components:{
	[Rate.name]:Rate,
  },
  methods:{
	machineClick(){
		this.machine.index=this.index
		this.$emit('machineClick',this.machine)
	}
  },
  data(){
	return{
		stars:0
	}
  },
  props:{
	machine:{
		type:Object,
		default(){
			return {}
		}
	},
	index:{
		type:Number,
		default:0,
	}
  },
  computed:{
	mStaus(){
		let statusObj={statusType:'空闲',className:'free',color:'red'}
		if(this.machine.status==0){
			statusObj.statusType='空闲'
			statusObj.className='free'
			statusObj.color='green'
		}else if(this.machine.status==1){
			statusObj.statusType='忙碌'
			statusObj.className='working'
			statusObj.color='#b75d00'
		}else{
			statusObj.statusType='准备完成'
			statusObj.className='on-ready'
			statusObj.color='blue'
		}
		return statusObj
	},
	useText(){
		return (this.machine.isuse==0?'停用':'启用')
	}
  },
  created(){
	this.machine.stars=this.machine.stars*1
  }
}
</script>

