	padding:10px 0;
	font-size:13px;
}
.working {
	border-color:red;
}
.free{
	border-color:green;
}
.on-ready{
	border-color:orange;
}
.machine-item .uimg{
	width:60px;
	height:60px;
	border-radius:50px;
	display:inline-block;
	line-height:60px;
	float:left;
	position:absolute;
	top:50%; 
	left:15px;
	transform:translate(0,-50%);
}
.info-box-x{
	float:right;
	width:75%;
}
.info-box-x div{
	padding:0 10px;
	overflow:hidden;
	line-height:25px;
}
.info-box-x span{
	float:right;
